.App {
  text-align: center;
  width: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
  box-sizing: border-box;
}

#root {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.wrapper {
  width: 100%;
  max-width: 800px;
} 

.product {
  width: 100%;
  display: flex;
  padding: 15px;
  border: 2px solid blue;
  margin-top: 15px;
  justify-content: space-between;
  align-items: center;
}

.product__btn {
  width: 20%;
}

.product__content {
  padding: 0 20px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product__title {
  margin-bottom: 0;
}

.statistic {
  padding: 20px;
  display: flex;
  justify-content: space-between;
}

.discount {
  padding: 20px;
  display: flex;
  justify-content: space-between;
}

.discount-wrapper {
  display: flex;
  justify-content: flex-start;
}

.discount button {
  width: 170px;
}

.discount input {
  margin: 0;
  padding: 10px;
  width: 150px;
}
